import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { VoucherBrandMonitoringComponent } from './voucher-brand-monitoring.component';

const routes: Routes = [
  {
    path:'',
    component: VoucherBrandMonitoringComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class VoucherBrandMonitoringRoutingModule { }
