import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { DomSanitizer } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService, SweetAlertService } from '@services';
import { ENDPOINTS } from '@shared';
import { FileStorageService } from 'app/core/services/file.storage.service';


@Component({
  selector: 'app-voucher-brand-monitoring',
  templateUrl: './voucher-brand-monitoring.component.html',
  styleUrls: ['./voucher-brand-monitoring.component.scss']
})
export class VoucherBrandMonitoringComponent implements OnInit {

  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild(MatTable, { static: false }) table: MatTable<Object>;
  dataSource: MatTableDataSource<any>

  displayedColumns: string[] = ['brand', 'amount', 'remarks', 'status'];
  companyList = [];
  brandPenalizedList = [];
  voucherList = [];

  title: string;
  brandPenalty: any;

  constructor(
    private router: Router,
    private apiService: ApiService,
    private sweetAlertService: SweetAlertService,
    private sanitizer: DomSanitizer,
    private fileStorageService: FileStorageService,
    private route: ActivatedRoute,
  ) { }

  ngOnInit(): void {
    this.brandPenalty = String(this.route.snapshot.paramMap.get('brand'));
    if (this.brandPenalty !== undefined) {
      console.log('+++++++', this.brandPenalty);
      this.title = 'Brand Penalty Monitoring';
      this.getBrandPenaltyMonitoring();
    } else {
      console.log('-----', this.brandPenalty);
      this.title = 'Voucher Penalty Monitoring';
      this.getVocherMonitoring();
    }

  }

  getVocherMonitoring() {
    console.log('VOUCHER MONITORING');
    this.apiService.findAll(`${ENDPOINTS.voucherMonitoring}?page=1&size=30`).subscribe((data: any) => {
      this.voucherList = data.data;
      this.dataSource = new MatTableDataSource<any>(this.voucherList);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    });
  }
  getBrandPenaltyMonitoring() {
    console.log('BRAND MONITORING');
    this.apiService.findAll(`${ENDPOINTS.penalizedBrandMonitoring}/monitoring?page=1&size=30`).subscribe((data: any) => {
      this.brandPenalizedList = data.data;
      this.dataSource = new MatTableDataSource<any>(this.brandPenalizedList);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    });
  }

}
